import React, { useState } from "react";
import { Contactus } from '../Contact Us/contactus';

function Service(props) {
    const [popbutton, setpopupbutton] = useState(false);
    return (
        <div className="main-wrapper-full">
            <h1 className="category-heading">Cources Details</h1>
            <div className="whole_loop">
                {props.cources.map((item, index) => {
                    return <div className="service-card" key={'cource' + index}>
                        <div className="service-image">
                            <img className="card-img" src={item.image_url} alt="" />
                        </div>
                        <div className="service-header">
                            <h1>{item.title}</h1>
                        </div>
                        <div className="service-content">
                            <p><span>Description</span> {item.description}</p>
                            <p><span>Duration: </span> {item.Duration}</p>
                            <p><span>Format: </span> {item.field_1}</p>
                            <p><span>Eligibility: </span> {item.Eligibility}</p>
                            <button className="btn-bg-black" onClick={() => { setpopupbutton(true) }}>Contact us</button>
                        </div>

                    </div>
                })}
            </div>
            <Contactus trigger={popbutton} setTrigger={setpopupbutton}></Contactus>
        </div>
    )
}
export default React.memo(Service);