import React from "react";

function About() {
  return (
    <div className="main-wrapper-full about">
      <div className="card-header">About</div>
      <p className="about-content">
        Welcome to the Art in Architecture Academy, where creativity and
        innovation intersect to transform the world around us. Our academy is
        dedicated to exploring the intersection of art and architecture,
        offering a unique and dynamic learning environment for individuals
        seeking to expand their knowledge and skills in these fields. At the Art
        in Architecture Academy, we believe that incorporating art into the
        design of buildings can create beautiful and functional spaces that
        enhance our quality of life. Our curriculum is designed to provide
        students with the tools and techniques they need to incorporate art into
        their architectural designs, whether it be through the use of color,
        texture, or other creative elements.
      </p>
      <iframe
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d472268.08476434305!2d87.82743873214322!3d22.36893838522745!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3a027183007bc3f5%3A0xc36f54bb3dc72df8!2sOm%20Sai%20Art%20in%20Architecture!5e0!3m2!1sen!2sin!4v1685209024795!5m2!1sen!2sin"
        width="100%"
        height="450"
        style={{ border: "0" }}
        allowFullScreen=""
        loading="lazy"
        referrerPolicy="no-referrer-when-downgrade"
      ></iframe>
    </div>
  );
}

export default React.memo(About);
