import React from "react";
import { NavLink } from "react-router-dom";

function Links(props) {
    return (
        <ul className="upper-nav-links">
            <li>
                <NavLink className={(navData) => (navData.isActive ? "active btn-bg-black" : 'btn-bg-black')} to={"/"}>
                    Home
                </NavLink>
            </li>
            <li>
                <NavLink className={(navData) => (navData.isActive ? "active btn-bg-black" : 'btn-bg-black')} to={"/about"}>
                    About us
                </NavLink>
            </li>
            <li>
                <NavLink className={(navData) => (navData.isActive ? "active btn-bg-black" : 'btn-bg-black')} to={"/course"}>
                    Cources
                </NavLink>
            </li>
            <li>
                <NavLink className={(navData) => (navData.isActive ? "active btn-bg-black" : 'btn-bg-black')} to={"/blog"}>
                    Blogs
                </NavLink>
            </li>
        </ul>
    );
}

export default Links;
