import "./App.css";
import React, { useEffect, useState } from "react";
import { Header } from "./Components/Header/Header";
import RouterConfig from "./RouterConfig/RouterConfig";
import Footer from "./Components/Footer/footer";
function App() {
  const [data, setdata] = useState([]);
  const [image, setimage] = useState([])
  useEffect(() => {
    const fetchData = () => {
      fetch("https://thecoderguy007.pythonanywhere.com/api/course/list/")
        .then((response) => {
          return response.json();
        })
        .then((dt) => {
          setdata(dt);
        });
      fetch(
        "https://thecoderguy007.pythonanywhere.com/api/common/carousel/?format=json"
      )
        .then((response) => {
          return response.json();
        })
        .then((img) => {
          setimage(img);
        });
    };
    fetchData();
  }, []);
  return (
    <div className="App">
      <div className="wrapper-black-bg">
        <Header />
      </div>
      <RouterConfig data={{images: image, cources: data}} />

      <div className="wrapper-white-bg">
        <Footer />
      </div>
    </div>
  );
}

export default App;
