import React from "react";
import { Link } from "react-router-dom";

function Card(props) {
    return (
        <div className="card">
            <div className="card-header">
                {props.cardData.title}
            </div>
            <div className="card-image">
                <img className="card-img" src={props.cardData.image_url} alt="" />
            </div>
            <div className="card-content">
                <p>{props.cardData.description}</p>
            </div>
            <Link to={"/course"}>
                <button className="view-more btn-bg-black">More</button>
            </Link>
        </div>
    );
}

export default Card;
