import React from "react";
import fbimg from '../../images/Footericon/fb.svg';
import instimg from '../../images/Footericon/insta.png';
import contants from "../../Constants/Constants";

function Footer(){
    return(
        <div className="footer">
            <div className="footer-desp">
            <p>© 2023 by Omsaiartnarchitecture </p>
            </div>
            <div className="footer-icon">
                <ul>
                    <li><a><img src={fbimg} alt="" /></a></li>
                    <li><a onClick={function() {
                        window.open(contants.INSTA_URL, '_blank');
                    }}><img src={instimg} alt="" /></a></li>
                </ul>
            </div>
        </div>
    )
}

export default React.memo(Footer);