import React, { useState } from "react";
import logo from '../../logo.svg';
import Links from "../../RouterConfig/Links";

function Header() {
  const [isSideNavOpened, setSideNavOpen] = useState(false);
  return (
    <div className="header-wrapper">
      <header className="header-x">
        <div className="logo"><img src={logo} alt="" /></div>
       <Links />
        <ul className="hamburger-link">
          <li>
            <button
              onClick={() => {
                let body = document.getElementsByTagName("body")[0];
                isSideNavOpened
                  ? body.classList.remove("sticky")
                  : body.classList.add("sticky");
                setSideNavOpen(!isSideNavOpened);
              }}
              className="btn-bg-black"
            >
              1
            </button>
          </li>
        </ul>
      </header>
      <header
        style={{ left: isSideNavOpened ? "0" : "100%" }}
        className="side-header-nav"
      >
        <Links />
      </header>
    </div>
  );
}

export { Header };
