import { useState } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";


function Corousel(props) {
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 2000,
    };
    return (
        <div className="corousel">
            <Slider {...settings}>
            {props.images.map((value, idx) => <div key={'carousel' + idx}><img src={value.image_url} alt="" /></div>)}
            </Slider>
        </div>
    );
}

export { Corousel }