import React from "react";
import { Routes, Route } from 'react-router-dom';
import Home from "../Components/Home/home";
import Service from "../Components/Service/service";
import About from "../Components/About/About";
import Blogs from "../Components/Bloges/Blogs";

function RouterConfig(props) {
    return (
        <Routes>
            <Route path='/' element={<Home images={props.data.images} cources={props.data.cources} />} />
            <Route path='/course' element={<Service cources={props.data.cources} />} />
            <Route path='/about' element={<About />} />
            <Route path='/blog' element={<Blogs />} />
        </Routes>
    );
}

export default RouterConfig;
